.task-details {
  background: black !important;
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 900px;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}

.table {
  width: 100%;
}

.table-header {
  background: #ff9900;
  display: flex;
  justify-content: space-between;
}

.header__item {
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.filter__link {
  color: black;
  text-decoration: none;
  position: relative;
  display: inline-block;
}
