.container {
  background: black !important;
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 700px;
  justify-content: center;
  align-items: center;
  padding: 50px;
}

h4 {
  padding: 2px;
  left: 0;
  width: 100%;
}

input {
  padding: 2px;
  width: 100%;
  height: 40px;
  border-radius: 4px;
  outline: none;
  cursor: text;
}

input:hover {
  border: 2px solid #ff9900;
}

select {
  width: 100%;
  height: 45px;
  border-radius: 4px;
  outline: none;
  cursor: text;
}

select:hover {
  border: 2px solid #ff9900;
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  margin: 8px;
}

button {
  margin: 6px;
  width: 8em;
  height: 4em;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

#reset:hover {
  border: 2px solid #ff9900;
  background: rgba(190, 189, 189, 0.945);
}

#submit {
  background: #ff9900;
}

#submit:hover {
  background: #ff9900a4;
  border: 2px solid #333;
}

.seeDetails {
  background: #333;
  color: #ff9900;
}
